// components/PersonaSelector.tsx
import React from 'react';

interface Props {
  persona: string;
  setPersona: (val: string) => void;
  customPrompt: string;
  setCustomPrompt: (val: string) => void;
}

export const PersonaSelector: React.FC<Props> = ({
  persona,
  setPersona,
  customPrompt,
  setCustomPrompt,
}) => {
  return (
    <div className="space-y-4">
      <div>
        <label className="block text-sm font-semibold text-gray-600 mb-1">Select Persona</label>
        <select
          className="w-full p-2 border rounded-full focus:outline-none focus:ring-2 focus:ring-blue-400"
          value={persona}
          onChange={(e) => setPersona(e.target.value)}
        >
          <option value="Friendly Assistant">Friendly Assistant</option>
          <option value="Sarcastic Expert">Sarcastic Expert</option>
          <option value="Emotional Mentor">Emotional Mentor</option>
          <option value="Stoic Analyst">Stoic Analyst</option>
        </select>
      </div>

      <div>
        <label className="block text-sm font-semibold text-gray-600 mb-1">
          Custom Pre-Prompt (Optional)
        </label>
        <textarea
          className="w-full p-2 border rounded-2xl h-24 focus:outline-none focus:ring-2 focus:ring-blue-400"
          placeholder="e.g., You are a humorous AI who likes puns..."
          value={customPrompt}
          onChange={(e) => setCustomPrompt(e.target.value)}
        />
      </div>
    </div>
  );
};