// components/ChatBox.tsx
import React, { useState, useEffect, useRef } from 'react';
import { ArrowUpCircle } from 'lucide-react'; // For the send button icon

// Interface for the props of the ChatInput component
interface ChatInputProps {
  onSendMessage: (message: string) => void;
  isLoading: boolean; // To disable input/button while loading
}

// Gemini-style ChatInput component (defined within ChatBox.tsx for this example)
const ChatInput: React.FC<ChatInputProps> = ({ onSendMessage, isLoading }) => {
  const [inputValue, setInputValue] = useState('');
  const textareaRef = useRef<HTMLTextAreaElement>(null);

  const handleInputChange = (event: React.ChangeEvent<HTMLTextAreaElement>) => {
    setInputValue(event.target.value);
    // Auto-resize textarea height
    if (textareaRef.current) {
      textareaRef.current.style.height = 'auto'; // Reset height
      textareaRef.current.style.height = `${textareaRef.current.scrollHeight}px`; // Set to scroll height
    }
  };

  const handleSend = () => {
    if (inputValue.trim() && !isLoading) {
      onSendMessage(inputValue.trim());
      setInputValue('');
      // Reset textarea height after sending
      if (textareaRef.current) {
        textareaRef.current.style.height = 'auto';
      }
    }
  };

  const handleKeyDown = (event: React.KeyboardEvent<HTMLTextAreaElement>) => {
    if (event.key === 'Enter' && !event.shiftKey) {
      event.preventDefault(); // Prevent new line on Enter
      handleSend();
    }
  };

  return (
    // Container for the input area (matches Gemini style)
    <div className="p-3 sm:p-4 bg-white border-t border-slate-200"> 
      <div className="flex items-end gap-2 p-1.5 sm:p-2 bg-slate-100 rounded-xl shadow-sm">
        {/* Optional: Button for attachments or other actions 
          <button className="p-2 text-slate-500 hover:text-indigo-600 rounded-full hover:bg-slate-200 transition-colors">
            <PlusCircle size={20} /> 
          </button> 
        */}
        <textarea
          ref={textareaRef}
          rows={1}
          className="flex-1 py-2 px-2.5 bg-transparent border-none focus:ring-0 resize-none overflow-y-auto text-sm text-slate-800 placeholder-slate-500 max-h-36" // Max height for textarea
          placeholder="Enter your message..."
          value={inputValue}
          onChange={handleInputChange}
          onKeyDown={handleKeyDown}
          disabled={isLoading}
        />
        <button
          onClick={handleSend}
          disabled={!inputValue.trim() || isLoading}
          className={`p-2 rounded-full transition-colors duration-150 ease-in-out
                      ${(inputValue.trim() && !isLoading) ? 'bg-indigo-600 text-white hover:bg-indigo-700' : 'bg-slate-200 text-slate-400 cursor-not-allowed'}`}
          aria-label="Send message"
        >
          <ArrowUpCircle size={22} />
        </button>
      </div>
    </div>
  );
};


// Props for the main ChatBox component
interface ChatBoxProps {
  selectedModel: string;
  apiKey: string;
  persona: string;
  customPrompt: string;
}

export const ChatBox: React.FC<ChatBoxProps> = ({
  selectedModel,
  apiKey,
  persona,
  customPrompt,
}) => {
  const [messages, setMessages] = useState<string[]>([]);
  const [isLoading, setIsLoading] = useState(false); // For loading state
  const messagesEndRef = useRef<HTMLDivElement>(null); // To scroll to bottom

  // Effect to scroll to the bottom when new messages are added
  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  }, [messages]);

  const handleSendMessageFromChatBox = async (messageToSend: string) => {
    if (!messageToSend.trim()) return;

    setIsLoading(true);
    const userMsg = `üë§ You: ${messageToSend}`;
    // Add user message and initial loading message
    setMessages(prevMessages => [...prevMessages, userMsg, `üß† ${selectedModel}: Thinking...`]);

    try {
      const res = await fetch('http://localhost:8000/chat', { // Ensure this URL is correct
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          message: messageToSend,
          model: selectedModel,
          api_key: apiKey,
          persona,
          custom_prompt: customPrompt,
        }),
      });

      const responseTextPrefix = `üß† ${selectedModel}: `;
      if (!res.ok) {
        let errorMsg = `Error: ${res.status} ${res.statusText}`;
        try {
          const errorData = await res.json();
          errorMsg = errorData.detail || errorData.error || errorMsg;
        } catch (e) {
          // Failed to parse error JSON, use default error
        }
        // Update the last message (which is "Thinking...") with the error
        setMessages(prevMessages => {
          const newMessages = [...prevMessages];
          if (newMessages.length > 0 && newMessages[newMessages.length - 1].includes("Thinking...")) {
            newMessages[newMessages.length - 1] = `‚ö†Ô∏è ${errorMsg}`;
          } else {
            newMessages.push(`‚ö†Ô∏è ${errorMsg}`); // Fallback if "Thinking..." not found
          }
          return newMessages;
        });
        setIsLoading(false);
        return;
      }

      const data = await res.json();
      // Update the last message (which is "Thinking...") with the actual response
      setMessages(prevMessages => {
        const newMessages = [...prevMessages];
        if (newMessages.length > 0 && newMessages[newMessages.length - 1].includes("Thinking...")) {
          newMessages[newMessages.length - 1] = `${responseTextPrefix}${data.response || 'Received an empty response.'}`;
        } else {
          newMessages.push(`${responseTextPrefix}${data.response || 'Received an empty response.'}`);
        }
        return newMessages;
      });

    } catch (error) {
      console.error("Chat API call failed:", error);
      // Update the last message (which is "Thinking...") with a network error
      setMessages(prevMessages => {
        const newMessages = [...prevMessages];
        if (newMessages.length > 0 && newMessages[newMessages.length - 1].includes("Thinking...")) {
          newMessages[newMessages.length - 1] = `‚ö†Ô∏è Network error. Could not reach the server.`;
        } else {
          newMessages.push(`‚ö†Ô∏è Network error. Could not reach the server.`);
        }
        return newMessages;
      });
    } finally {
      setIsLoading(false);
    }
  };

  return (
    // Main container for the ChatBox, designed to fill its parent in pages/index.tsx
    <div className="flex flex-col h-full bg-white overflow-hidden">
      {/* Message display area */}
      <div className="flex-1 p-4 sm:p-6 space-y-3 overflow-y-auto">
        {messages.map((msg, i) => (
          <div 
            key={i} 
            className={`p-2.5 rounded-lg max-w-xl lg:max-w-2xl xl:max-w-3xl break-words whitespace-pre-wrap text-sm
                        ${msg.startsWith('üë§') ? 'bg-indigo-500 text-white self-end ml-auto rounded-br-none' : 'bg-slate-100 text-slate-800 self-start mr-auto rounded-bl-none'}`}
          >
            {/* Removing the prefix for display if you prefer */}
            {msg.startsWith('üë§ You: ') ? msg.substring('üë§ You: '.length) : 
             msg.startsWith(`üß† ${selectedModel}: `) ? msg.substring(`üß† ${selectedModel}: `.length) : 
             msg.startsWith('‚ö†Ô∏è ') ? msg.substring('‚ö†Ô∏è '.length) : msg}
          </div>
        ))}
        <div ref={messagesEndRef} /> {/* Invisible element to scroll to */}
      </div>
      {/* Gemini-style input area */}
      <ChatInput onSendMessage={handleSendMessageFromChatBox} isLoading={isLoading} />
    </div>
  );
};
